@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "SegoeUI";
  font-display: swap;
  src: url("../fonts/SegoeUI.woff2") format("woff2"), url("../fonts/SegoeUI.woff") format("woff");
}
/* Шрифт "Segoe UI SemiBold" */
@font-face {
  font-style: normal;
  font-weight: 600;
  font-family: "SegoeUISemiBold";
  font-display: swap;
  src: url("../fonts/SegoeUI-SemiBold.woff2") format("woff2"), url("../fonts/SegoeUI-SemiBold.woff") format("woff");
}
/* Шрифт "Segoe UI Bold" */
@font-face {
  font-style: normal;
  font-weight: 700;
  font-family: "SegoeUIBold";
  font-display: swap;
  src: url("../fonts/SegoeUI-Bold.woff2") format("woff2"), url("../fonts/SegoeUI-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 20px;
  font-family: "SegoeUI", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
  border: none;
  background-color: inherit;
  padding: 0;
}

h1, h2, h3, p {
  margin: 0;
  padding: 0;
}

ul, li {
  margin: 0;
  padding: 0;
  list-style: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
  padding: 0 16px;
}
@media (min-width: 1400px) {
  .container {
    padding: 0 24px;
  }
}

.btn {
  cursor: pointer;
}

.btn__change {
  font-size: 16px;
  font-family: "SegoeUIBold";
  line-height: 24px;
  color: #CB11AB;
  margin-top: 4px;
}

.payment__modal {
  width: 100%;
  max-width: 400px;
  min-height: 408px;
  background-color: red;
  z-index: 10;
}

.modal {
  position: absolute;
  border-radius: 16px;
}

.payment__modal-title {
  margin-bottom: 16px;
}

.payment__modal-container {
  display: flex;
  flex-direction: column;
  height: 300px;
  justify-content: space-between;
}

.payment__modal-wrapper {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.payment__modal-label {
  display: flex;
}

.mir {
  display: flex;
  flex-direction: row;
  height: 24px;
  align-items: center;
  position: relative;
  margin-left: 60px;
}
.mir::before {
  content: "";
  position: absolute;
  background-image: url("../img/icons/credit-mir.png");
  width: 32px;
  height: 24px;
  left: -40px;
}

.payment__modal-button {
  width: 164px;
  height: 64px;
  border-radius: 8px;
  background-color: violet;
}

.checkbox .checkbox__container {
  position: absolute;
  width: 22px;
  height: 22px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 6px;
  z-index: 3;
  cursor: pointer;
}
.checkbox .checkbox__container::after {
  content: "";
  position: absolute;
  width: 6px;
  height: 12px;
  top: 3px;
  left: 8px;
  border-right: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  transform: rotate(45deg);
  opacity: 100%;
  display: none;
}

.checkbox input:checked + .checkbox__container::after {
  display: block;
}

.checkbox input:checked + .checkbox__container {
  background-color: #CB11AB;
  border: none;
}

.checkbox input {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  overflow: hidden;
}

.header {
  position: relative;
  width: 100%;
  min-height: 80px;
  background-color: #6725A8;
  padding: 14px 13px;
}
@media (min-width: 1024px) {
  .header {
    min-height: 120.2px;
    padding-left: 24px;
    padding-right: 20px;
    padding-top: 16.3px;
  }
}
.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0;
}
@media (min-width: 1024px) {
  .header__container {
    min-height: 56px;
    justify-content: start;
  }
}
.header__burger {
  width: 28px;
  height: 28px;
  padding: 4.75px 2.75px;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header__burger {
    width: 48px;
    height: 48px;
    border: 1px solid #ffffff;
    opacity: 0.45;
    border-radius: 8px;
  }
}
.header__burger span {
  position: relative;
  display: block;
  width: 22.5px;
  height: 2.5px;
  border-radius: 4px;
  background-color: #ffffff;
}
@media (min-width: 1024px) {
  .header__burger span {
    height: 4px;
    width: 28px;
    left: 5.9px;
  }
}
.header__burger span::after {
  content: "";
  position: absolute;
  display: block;
  top: -7.8px;
  width: 22.5px;
  height: 2.5px;
  border-radius: 4px;
  background-color: inherit;
}
@media (min-width: 1024px) {
  .header__burger span::after {
    height: 4px;
    width: 28px;
    top: 9.4px;
  }
}
.header__burger span::before {
  content: "";
  position: absolute;
  display: block;
  top: 7.8px;
  width: 16.5px;
  height: 2.5px;
  border-radius: 4px;
  background-color: inherit;
}
@media (min-width: 1024px) {
  .header__burger span::before {
    height: 4px;
    width: 28px;
    top: -10.1px;
  }
}
@media (min-width: 1024px) {
  .header__logo {
    max-width: 139.2px;
  }
}
.header__logo h1 {
  color: #ffffff;
  font-size: 19.5px;
  font-family: "SegoeUISemiBold";
  font-weight: 400;
  line-height: 24px;
  letter-spacing: 2.4px;
  text-transform: uppercase;
  margin-top: -4.2px;
  margin-left: 1.2px;
}
@media (min-width: 1024px) {
  .header__logo h1 {
    font-size: 21.3px;
    letter-spacing: 2.2px;
    margin: 0;
    margin-left: 16.8px;
  }
}
.nav--mobile {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 38.5px;
  z-index: 10;
  position: fixed;
  width: 100%;
  height: 56px;
  bottom: 0;
  left: 0;
  background-color: #ffffff;
}

.nav__link {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  fill: transparent;
  width: 28px;
  height: 28px;
}

.nav__icon svg {
  fill: #CCCCCC;
}
.nav__icon svg:hover {
  fill: #CB11AB;
}

.nav__icon--count {
  position: absolute;
  font-size: 10px;
  color: #ffffff;
  font-family: "SegoeUISemiBold";
  min-width: 16px;
  min-height: 16px;
  background-color: #F55123;
  top: -6.5px;
  right: -5.5px;
  border-radius: 9px;
  padding-inline: 5px;
  line-height: 16px;
  text-align: center;
}

.search {
  width: 28px;
  height: 28px;
  cursor: pointer;
}

.search__icon {
  fill: #ffffff;
  margin-top: 4px;
}

@media (min-width: 1024px) {
  .wrapper__container {
    width: 640px;
  }
}
.order {
  position: relative;
  z-index: 3;
  background-color: aquamarine;
  border-radius: 23.8px;
  margin-top: -32.6px;
}
@media (min-width: 1024px) {
  .order {
    display: flex;
    gap: 20px;
    justify-content: space-between;
    padding: 24px;
    padding-top: 22px;
  }
}
.order__container h2 {
  font-size: 20px;
  font-weight: bold;
  font-family: "SegoeUI";
  line-height: 28px;
  margin-bottom: 6px;
}

.basket__container {
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .basket__container {
    padding: 0;
    padding-top: 13.9px;
  }
}
.basket__title {
  font-size: 20px;
  font-family: "SegoeUIBold";
  line-height: 25.2px;
  letter-spacing: -0.2px;
  margin-bottom: 17.9px;
}
@media (min-width: 1024px) {
  .basket__title {
    font-size: 28px;
    line-height: 36px;
    letter-spacing: -0.3px;
    margin-bottom: 25.1px;
  }
}
.basket__accordion {
  height: 24px;
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 16px;
}
.basket__accordion--absence {
  margin-bottom: 18px;
}

.basket__list {
  display: flex;
  flex-direction: column;
  gap: 15.2px;
  transition: all 0.3s;
}

.basket__list--closed {
  display: none;
  transition: all 0.3s;
}

.basket__item {
  display: flex;
  flex-direction: column;
  border-top: 1px solid #9797AF;
  padding-top: 11.9px;
}
@media (min-width: 1024px) {
  .basket__item {
    flex-direction: row;
  }
}
.available {
  padding-top: 16px;
  margin-bottom: 47px;
}

.available__title {
  margin-left: 11px;
  font-size: 16px;
  font-family: "SegoeUI";
  line-height: 18.4px;
  margin-bottom: 2.3px;
}

.accordion__arrow {
  fill: #9797AF;
}

.accordion__checkbox {
  display: flex;
  align-items: center;
  min-width: 125px;
  cursor: pointer;
}

.accordion__checkbox .checkbox__container {
  position: relative;
}

.accordion__checkbox input:checked + .checkbox__container::after {
  display: block;
}

.accordion__button {
  display: flex;
  align-items: center;
  justify-content: end;
  width: 100%;
  height: 100%;
  cursor: pointer;
}

.accordion__button span {
  display: block;
  position: relative;
  width: 44px;
  height: 44px;
}

.accordion__arrow {
  position: absolute;
  left: calc(50% - 0.4px);
  top: calc(50% - 4.9px);
  transform: rotate(0deg);
  transition: transform 0.3s;
}
@media (min-width: 1024px) {
  .accordion__arrow {
    left: calc(50% + 3.5px);
    top: calc(50% - 3.9px);
  }
}
.accordion__arrow--closed {
  transform: rotate(180deg);
  transition: transform 0.3s;
}

.control {
  position: relative;
}

.control__number {
  margin-bottom: 4px;
}

.control__number input {
  font-size: 14px;
  font-family: "SegoeUI";
  line-height: 20px;
  border: none;
  background-color: inherit;
  outline: none;
  width: 32px;
  height: 100%;
  text-align: center;
}

.control__minus {
  cursor: pointer;
  position: relative;
  width: 24px;
  height: 32px;
  border-radius: 4px;
}
.control__minus::after {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 9px;
  height: 1px;
  top: calc(50% - 1.7px);
  left: 42.7%;
}

.control__plus {
  cursor: pointer;
  position: relative;
  width: 24px;
  height: 32px;
  border-radius: 4px;
}
.control__plus::before {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 1px;
  height: 9.1px;
  top: calc(50% - 4.9px);
  left: 43.7%;
}
.control__plus::after {
  content: "";
  position: absolute;
  background-color: #000000;
  width: 9px;
  height: 1px;
  top: calc(50% - 1.7px);
  left: calc(50% - 5.7px);
}

.product__checkbox .checkbox__container {
  position: absolute;
  top: 4px;
  left: 4px;
  background-color: #ffffff;
}

.product__card {
  display: flex;
  position: relative;
  margin-bottom: 12px;
}

.product__card--phone {
  margin-bottom: 10.1px;
}

.product__card--pencils {
  margin-bottom: 12px;
}

.product__info {
  display: flex;
  flex-direction: column;
  margin-left: 8px;
  gap: 2px;
  font-size: 14px;
  line-height: 20px;
  max-width: 192px;
}

.product__count {
  display: flex;
  align-items: center;
  max-height: 32px;
}
@media (min-width: 1024px) {
  .product__count {
    flex-direction: column;
  }
}
.product__price {
  display: flex;
  align-items: center;
  margin-top: 0.2px;
}
.product__size {
  position: absolute;
  font-size: 14px;
  font-family: "SegoeUI";
  line-height: 20px;
  padding: 1px 3.3px;
  background-color: #ffffff;
  border-radius: 6px;
  text-align: center;
  bottom: 4px;
  left: 4px;
  z-index: 5;
}

.product__img {
  width: 88px;
  height: 106px;
}

.available .product__link {
  position: relative;
}
.available .product__link:after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: 2;
  border-radius: 8px;
  max-width: 80px;
  max-height: 106px;
}

.absence .product__link {
  filter: saturate(0%);
}

.product__link--absence {
  cursor: default;
}
.product__link--absence img {
  filter: grayscale(100%);
}

.product__count--absence {
  margin-left: auto;
